'use client';

import React, { useState, useEffect } from 'react';
import { Event } from '@/types';
import { ChevronDown, ChevronUp, AlertTriangle, CheckCircle2, Shield, Zap, Package, MousePointer2 } from 'lucide-react';
import { cn } from '@/lib/utils';
import PredictionTable from '@/components/ui/PredictionTable'; // Absolute path
import { Badge } from '@/components/ui/Badge'; // Absolute path
import { joinCycle } from '@/app/actions'; // Import the Server Action

interface SiteAccordionProps {
  events: Event[];
  isSubscribed: boolean;
  cycleId: string;   // <--- Passed down to identify the cycle
  userId?: string;   // <--- Passed down to identify the user
}

export default function SiteAccordion({ events, isSubscribed, cycleId, userId }: SiteAccordionProps) {
  const [openEventId, setOpenEventId] = useState<number | null>(events[0]?.id || null);

  // In a real app, we'd fetch this from the DB (via props). 
  // For MVP, we'll track "local session" joining if props aren't fully wired yet, 
  // but the Server Action will handle the real DB persistence.
  const [hasJoined, setHasJoined] = useState(false);

  return (
    <div className="space-y-4">
      {events.map((event) => (
        <AccordionItem 
          key={event.id} 
          event={event} 
          isOpen={openEventId === event.id} 
          onToggle={() => setOpenEventId(openEventId === event.id ? null : event.id)}
          isSubscribed={isSubscribed}
          cycleId={cycleId}
          userId={userId}
          hasJoined={hasJoined}
          setHasJoined={setHasJoined}
        />
      ))}
    </div>
  );
}

// --- SUB-COMPONENT ---
function AccordionItem({ event, isOpen, onToggle, isSubscribed, cycleId, userId, hasJoined, setHasJoined }: any) {
  const [activeTab, setActiveTab] = useState<'strat_a' | 'strat_b'>('strat_a');
  const [loading, setLoading] = useState(false);
  
  // Selection State
  const [bundleMode, setBundleMode] = useState<'full' | 'custom'>('full');

  const handleCommit = async () => {
    if (!userId) return; // Should catch at login level, but safety first
    setLoading(true);

    // Call Server Action
    const result = await joinCycle(cycleId, userId, bundleMode, event.platform);
    
    setLoading(false);
    if (result.success) {
      setHasJoined(true); // Unlock UI
    } else {
      alert("Error joining cycle: " + result.message);
    }
  };

  return (
    <div className="border border-gray-800 rounded-xl bg-gray-900/30 overflow-hidden transition-all">
      
      {/* HEADER */}
      <button 
        onClick={onToggle}
        className="w-full flex items-center justify-between p-4 hover:bg-white/5 transition-colors"
      >
        <div className="flex items-center space-x-4">
          <div className={cn("p-2 rounded-lg", isOpen ? "bg-cyan-500/20 text-cyan-400" : "bg-gray-800 text-gray-400")}>
            {event.platform === 'SportPesa' ? 'âš½' : 'ðŸŽ¯'}
          </div>
          <div className="text-left">
            <h3 className="text-lg font-bold text-white">{event.platform}</h3>
            <p className="text-xs text-gray-400">{event.event_name} â€¢ Week {event.week_number}</p>
          </div>
        </div>
        <div className="flex items-center space-x-3">
          {(!isSubscribed || !hasJoined) && <Badge variant="warning">LOCKED</Badge>}
          {isOpen ? <ChevronUp className="w-5 h-5 text-gray-500" /> : <ChevronDown className="w-5 h-5 text-gray-500" />}
        </div>
      </button>

      {/* CONTENT BODY */}
      {isOpen && (
        <div className="border-t border-gray-800 p-4 bg-black/20">
          
          {/* STATE 1: SELECTION & CHECKLIST (If user hasn't joined yet) */}
          {isSubscribed && !hasJoined ? (
            <div className="py-6 space-y-6 max-w-lg mx-auto animate-in fade-in zoom-in-95 duration-300">
              
              <div className="text-center">
                <h4 className="text-xl font-bold text-white mb-2">Configure Your Entry</h4>
                <p className="text-gray-400 text-xs">Select your risk profile for this cycle.</p>
              </div>

              {/* BUNDLE SELECTION CARDS */}
              <div className="grid grid-cols-2 gap-4">
                {/* Full Bundle Option */}
                <div 
                  onClick={() => setBundleMode('full')}
                  className={cn(
                    "cursor-pointer p-4 rounded-xl border transition-all relative overflow-hidden",
                    bundleMode === 'full' 
                      ? "bg-cyan-900/20 border-cyan-500" 
                      : "bg-gray-900 border-gray-800 opacity-60 hover:opacity-100"
                  )}
                >
                  {bundleMode === 'full' && (
                    <div className="absolute top-2 right-2 text-cyan-500"><CheckCircle2 className="w-4 h-4"/></div>
                  )}
                  <Package className={cn("w-8 h-8 mb-3", bundleMode === 'full' ? "text-cyan-400" : "text-gray-500")} />
                  <h5 className="font-bold text-sm text-white">Full Bundle</h5>
                  <p className="text-[10px] text-gray-400 mt-1">Recommended. Covers all platforms. Maximize variance.</p>
                </div>

                {/* Custom Option */}
                <div 
                  onClick={() => setBundleMode('custom')}
                  className={cn(
                    "cursor-pointer p-4 rounded-xl border transition-all relative overflow-hidden",
                    bundleMode === 'custom' 
                      ? "bg-yellow-900/20 border-yellow-500" 
                      : "bg-gray-900 border-gray-800 opacity-60 hover:opacity-100"
                  )}
                >
                  {bundleMode === 'custom' && (
                    <div className="absolute top-2 right-2 text-yellow-500"><CheckCircle2 className="w-4 h-4"/></div>
                  )}
                  <MousePointer2 className={cn("w-8 h-8 mb-3", bundleMode === 'custom' ? "text-yellow-400" : "text-gray-500")} />
                  <h5 className="font-bold text-sm text-white">Single Site</h5>
                  <p className="text-[10px] text-gray-400 mt-1">High Risk. If other sites hit the bonus, you miss out.</p>
                </div>
              </div>

              {/* WARNING MESSAGE (If Custom Selected) */}
              {bundleMode === 'custom' && (
                <div className="bg-yellow-900/10 border border-yellow-900/50 p-3 rounded-lg flex items-start space-x-3">
                  <AlertTriangle className="w-5 h-5 text-yellow-500 flex-shrink-0" />
                  <p className="text-xs text-yellow-200/80">
                    <strong>Warning:</strong> You are reducing your win probability. If the cycle hits on a different platform, you will NOT receive the Rollover Credit.
                  </p>
                </div>
              )}

              {/* COMMIT BUTTON */}
              <button 
                onClick={handleCommit}
                disabled={loading}
                className={cn(
                  "w-full py-4 font-bold rounded-lg transition-all flex items-center justify-center shadow-lg",
                  loading ? "bg-gray-700 cursor-not-allowed" : "bg-cyan-600 hover:bg-cyan-500 text-black shadow-cyan-900/20"
                )}
              >
                {loading ? 'SYNCING...' : `CONFIRM & REVEAL (${bundleMode === 'full' ? 'FULL' : 'SINGLE'})`}
              </button>
            </div>
          ) : (
            
            /* STATE 2: THE REVEAL (Tabs + Table) */
            /* Shows if user IS subscribed AND HAS joined */
            <div className="space-y-4 animate-in fade-in duration-500">
              
              {/* TABS */}
              <div className="flex p-1 bg-gray-800/50 rounded-lg">
                <button
                  onClick={() => setActiveTab('strat_a')}
                  className={cn(
                    "flex-1 flex items-center justify-center py-2 text-sm font-bold rounded-md transition-all",
                    activeTab === 'strat_a' ? "bg-cyan-900/50 text-cyan-400 shadow-sm" : "text-gray-500 hover:text-gray-300"
                  )}
                >
                  <Shield className="w-4 h-4 mr-2" /> STRATEGY A (SHIELD)
                </button>
                <button
                  onClick={() => setActiveTab('strat_b')}
                  className={cn(
                    "flex-1 flex items-center justify-center py-2 text-sm font-bold rounded-md transition-all",
                    activeTab === 'strat_b' ? "bg-purple-900/50 text-purple-400 shadow-sm" : "text-gray-500 hover:text-gray-300"
                  )}
                >
                  <Zap className="w-4 h-4 mr-2" /> STRATEGY B (SWORD)
                </button>
              </div>

              {/* DATA TABLE */}
              <PredictionTable 
                predictions={event.predictions || []} 
                isSubscribed={isSubscribed && hasJoined}
                strategyMode={activeTab}
              />
            </div>
          )}
        </div>
      )}
    </div>
  );
}